  @font-face {
    font-family: 'New York Extra Large Bold';
    font-style: normal;
    font-weight: normal;
    font-display: swap;
    src: local('New York Extra Large Bold'), url('../fonts/NewYorkExtraLargeBold.woff') format('woff');
    }


    @font-face {
        font-family: 'SF UI Display';
